import indexList from '../../components/index/indexList/index-list.vue';
import swiperTab from '../../components/index/swiperTab/swiper-tab.vue';
import loadMore from '../../components/common/loadmore.vue';
import noThing from '../../components/common/noThing.vue';
export default {
	components: {
		indexList,
		swiperTab,
		loadMore,
		noThing
	},
	data() {
		return {
			swiperheight: '',
			tabIndex: 0,
			tabBars: [
				{ name: '关注', id: 'guanzhu' },
				{ name: '推荐', id: 'tuijian' },
				{ name: '体育', id: 'tiyu' },
				{ name: '热点', id: 'redian' },
				{ name: '财经', id: 'caijing' },
				{ name: '娱乐', id: 'yule' }
			],
			newsList: [
				{
					loadtext: '上拉加载更多',
					list: [
						{
							userpic: '/static/demo/userpic/1.jpg',
							username: '昵称',
							isguanzhu: false,
							title: '我是标题',
							type: 'img', //img:图文 video：视频
							titlepic: '/static/demo/datapic/1.jpg',
							infonum: {
								index: 0, //0:没有操作 1.顶 2.踩
								dingnum: 11,
								cainum: 10
							},
							commentnum: 2,
							sharenum: 10
						},
						{
							userpic: '/static/demo/userpic/1.jpg',
							username: '昵称',
							isguanzhu: true,
							title: '我是标题',
							type: 'video', //img:图文 video：视频
							titlepic: '/static/demo/datapic/1.jpg',
							playnum: '20w',
							long: '2:17',
							infonum: {
								index: 1, //0:没有操作 1.顶 2.踩
								dingnum: 11,
								cainum: 10
							},
							commentnum: 2,
							sharenum: 10
						},
						{
							userpic: '/static/demo/userpic/1.jpg',
							username: '昵称',
							isguanzhu: true,
							title: '我是标题',
							type: 'video', //img:图文 video：视频
							titlepic: '/static/demo/datapic/1.jpg',
							playnum: '20w',
							long: '2:17',
							infonum: {
								index: 2, //0:没有操作 1.顶 2.踩
								dingnum: 11,
								cainum: 10
							},
							commentnum: 2,
							sharenum: 10
						}
					]
				},
				{
					loadtext: '上拉加载更多',
					list: [
						{
							userpic: '/static/demo/userpic/1.jpg',
							username: '昵称',
							isguanzhu: false,
							title: '我是标题',
							type: 'img', //img:图文 video：视频
							titlepic: '/static/demo/datapic/1.jpg',
							infonum: {
								index: 0, //0:没有操作 1.顶 2.踩
								dingnum: 11,
								cainum: 10
							},
							commentnum: 2,
							sharenum: 10
						}
					]
				},
				{ loadtext: '上拉加载更多', list: [] },
				{ loadtext: '上拉加载更多', list: [] },
				{ loadtext: '上拉加载更多', list: [] },
				{ loadtext: '上拉加载更多', list: [] }
			]
		};
	},
	onLoad() {
		let that = this;
		uni.getSystemInfo({
			success: function(res) {
				let height = res.windowHeight - uni.upx2px(101);
				that.swiperheight = height;
				console.log(height);
			}
		});
		uni.request({
			url:"http://ceshi2.dishait.cn/api/v1/postclass",
			method:"GET",
			success: (res) => {
				console.log(res)
			},
			fail: (err) => {
				console.log(err)
			}
		})

	},
	methods: {
		tabtap(index) {
			this.tabIndex = index;
		},
		tabChange(e) {
			// console.log(JSON.stringify(e.detail.current));
			this.tabIndex = e.detail.current;
		},
		//上拉加载
		loadmore(index) {
			console.log('上拉加载');
			if (this.newsList[index].loadtext != '上拉加载更多') {
				return;
			}
			setTimeout(() => {
				let obj = {
					userpic: '/static/demo/userpic/1.jpg',
					username: '昵称',
					isguanzhu: false,
					title: '',
					type: 'img', //img:图文 video：视频
					titlepic: '/static/demo/datapic/1.jpg',
					infonum: {
						index: 0, //0:没有操作 1.顶 2.踩
						dingnum: 11,
						cainum: 10
					},
					commentnum: 2,
					sharenum: 10
				};
				this.newsList[index].list.push(obj);
				this.newsList[index].loadtext = '加载中...';
			}, 1000);
			// this.newsList[index].loadtext = '没有更多数据了';
		},
		//监听原生搜索框点击事件
		onNavigationBarSearchInputClicked() {
			// console.log('点击了搜索框')
			uni.navigateTo({
				url: '../search/search'
			});
		},
		onNavigationBarButtonTap(e) {
			switch (e.index) {
				case 0:
					console.log('进入签到页面');
					break;
				case 1:
					console.log('进入发布页面');
					uni.navigateTo({
						url: '../addInput/addInput'
					});
					break;
			}
		}
	}
};

